// Global styles
@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,700;1,100&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Average+Sans&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Hind+Madurai&display=swap');

* {
	margin: 0;
	padding: 0;
	overflow: hidden;
	outline: none;
}

*,
*::after,
*::before {
	box-sizing: border-box;
}

body {
	margin: 0;
	font-family: 'Hind Madurai', sans-serif;
	font-size: 16px;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	color: white;
}

h1 {
	font-family: 'Montserrat', sans-serif;
	color: white;
}

h2 {
	font-family: 'Average Sans', sans-serif;
	color: white;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

.background {
	background: $background;
	min-height: 100vh;
	display: flex;
}

.circle1,
.circle2 {
	height: calc(max(50vw, 50vh));
	width: calc(max(50vw, 50vh));
	position: fixed;
	border-radius: 50%;
	filter: blur(40px);
}

.circle1 {
	background: #3f3e5c;
	background: linear-gradient(280deg, #3f3e5c 0%, #3a3c4a 100%);
	top: -10vw;
	left: -10vw;
}

.circle2 {
	background: #34334a;
	background: linear-gradient(253deg, #34334a 0%, #2c2e3d 100%);
	bottom: -15vw;
	right: -5vw;
}

.main-content {
	display: flex;
	position: relative;
	z-index: 1;
	width: 100%;
	height: 100vh;
}
